import React from "react";
import { Result, Typography } from "antd";

export default function Update() {
  return (
    <Result
      status="success"
      title="New Update Available!"
      subTitle={`The update will be applied automatically after restarting Universal Automation Desktop.`}
    >
      <Typography.Paragraph>
        <Typography.Text
          strong
          style={{
            fontSize: 16,
          }}
        >
          <a href="https://github.com/ironmansoftware/universal-automation/blob/master/CHANGELOG.md">
            View the Changelog
          </a>
        </Typography.Text>
      </Typography.Paragraph>
    </Result>
  );
}
